@keyframes shake-slow {
  2% {
    transform: translate(0px, -7px) rotate(2.5deg)
  }
  4% {
    transform: translate(6px, 3px) rotate(-.5deg)
  }
  6% {
    transform: translate(6px, -5px) rotate(.5deg)
  }
  8% {
    transform: translate(3px, 3px) rotate(-1.5deg)
  }
  10% {
    transform: translate(-4px, 5px) rotate(1.5deg)
  }
  12% {
    transform: translate(2px, 7px) rotate(2.5deg)
  }
  14% {
    transform: translate(0px, 6px) rotate(-1.5deg)
  }
  16% {
    transform: translate(-9px, 5px) rotate(-2.5deg)
  }
  18% {
    transform: translate(4px, -8px) rotate(-.5deg)
  }
  20% {
    transform: translate(2px, 9px) rotate(3.5deg)
  }
  22% {
    transform: translate(-5px, 1px) rotate(-2.5deg)
  }
  24% {
    transform: translate(-2px, -8px) rotate(.5deg)
  }
  26% {
    transform: translate(4px, -2px) rotate(-.5deg)
  }
  28% {
    transform: translate(-4px, 9px) rotate(1.5deg)
  }
  30% {
    transform: translate(-4px, -3px) rotate(3.5deg)
  }
  32% {
    transform: translate(-2px, 6px) rotate(-2.5deg)
  }
  34% {
    transform: translate(4px, -4px) rotate(-.5deg)
  }
  36% {
    transform: translate(-1px, 6px) rotate(.5deg)
  }
  38% {
    transform: translate(8px, 8px) rotate(1.5deg)
  }
  40% {
    transform: translate(9px, -2px) rotate(3.5deg)
  }
  42% {
    transform: translate(-2px, -9px) rotate(.5deg)
  }
  44% {
    transform: translate(-1px, 10px) rotate(-1.5deg)
  }
  46% {
    transform: translate(-1px, 1px) rotate(-.5deg)
  }
  48% {
    transform: translate(6px, -8px) rotate(2.5deg)
  }
  50% {
    transform: translate(-1px, -7px) rotate(-1.5deg)
  }
  52% {
    transform: translate(0px, 1px) rotate(-1.5deg)
  }
  54% {
    transform: translate(1px, -8px) rotate(-2.5deg)
  }
  56% {
    transform: translate(-4px, 2px) rotate(1.5deg)
  }
  58% {
    transform: translate(10px, -7px) rotate(-2.5deg)
  }
  60% {
    transform: translate(-2px, -4px) rotate(-1.5deg)
  }
  62% {
    transform: translate(-3px, 3px) rotate(1.5deg)
  }
  64% {
    transform: translate(8px, 2px) rotate(-1.5deg)
  }
  66% {
    transform: translate(-4px, -1px) rotate(1.5deg)
  }
  68% {
    transform: translate(-1px, -2px) rotate(-1.5deg)
  }
  70% {
    transform: translate(8px, 8px) rotate(.5deg)
  }
  72% {
    transform: translate(-8px, -3px) rotate(-2.5deg)
  }
  74% {
    transform: translate(6px, 5px) rotate(.5deg)
  }
  76% {
    transform: translate(4px, -9px) rotate(1.5deg)
  }
  78% {
    transform: translate(-2px, -6px) rotate(3.5deg)
  }
  80% {
    transform: translate(1px, 0px) rotate(1.5deg)
  }
  82% {
    transform: translate(-4px, 6px) rotate(-2.5deg)
  }
  84% {
    transform: translate(-4px, -3px) rotate(1.5deg)
  }
  86% {
    transform: translate(7px, 10px) rotate(2.5deg)
  }
  88% {
    transform: translate(-3px, -2px) rotate(1.5deg)
  }
  90% {
    transform: translate(8px, -3px) rotate(3.5deg)
  }
  92% {
    transform: translate(0px, 3px) rotate(1.5deg)
  }
  94% {
    transform: translate(5px, -5px) rotate(-2.5deg)
  }
  96% {
    transform: translate(7px, -2px) rotate(-.5deg)
  }
  98% {
    transform: translate(-6px, 0px) rotate(3.5deg)
  }
  0%,
  100% {
    transform: translate(0, 0) rotate(0)
  }
}

.services__title  {
  text-transform: uppercase;
  margin-left: 0;
  margin-bottom: 50px;
}

.services__list {
  display: flex;
  justify-content: space-between;
  opacity: 0;
  transform: translateY(150vh);
}

.services__item {

  &:nth-of-type(even) .services__item-title {
    color: $prime-color;
  }

  &:hover h3,
  &:active h3 {
    animation-name: shake-slow;
    animation-duration: 10s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
  }

  & ul {
    margin-top: -1px;
    padding: 14px 28px;
    border: 1px solid $prime-color;
    list-style: none;
    display: none;

    & li {
      margin-bottom: 25px;
      font-weight: 600;
      font-size: 16px;
    }

    & li:last-of-type {
      margin-bottom: 0;
    }
  }
}

.services__item-title {
  display: inline-block;
  font-weight: 800;
  font-size: 3.8vh;
  text-transform: uppercase;
  border-bottom: 1px solid $prime-color;
  cursor: pointer;
}

.services__block-button {
  text-align: center;
  transform: translate(50px, 150vh);
}

.services__button {
  margin-top: 7%;
  text-transform: capitalize;
  padding: 31px 145px 33px;
}

.swiper-slide-active .services__list {
  opacity: 1;
  transform: translate(0);
  transition: opacity 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.8s ,transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
}

.swiper-slide-active .services__block-button {
  transform: translate(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 2.5s;
}

@media (max-width: $mobile-width) {
  .services__title {
    margin-bottom: 30px;
  }

  .services__list {
    flex-wrap: wrap;
  }

  .services__item {
    width: 100%;
    margin-bottom: 30px;
    text-align: left;
  }

  .services__item-title {
    font-size: 28px;
    width: 100%;
  }

  .services__button {
    margin-top: 20px;
    width: 100%;
    padding: 21px 25px 23px;
  }
}
