.review__box {
  height: 100%;
  position: relative;

  &::after {
    content: "";
    width: 50%;
    height: 100%;
    background: url('../img/review.png') no-repeat top right/auto 100%;
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(100vw);
  }
}

.review__title {
  margin-left: 0;
  font-weight: 800;
  font-size: 80px;
}

.review__text {
  width: 570px;
  max-width: 100%;
  font-size: 19px;
  color: #7B7B7B;
  margin-bottom: 46px;
  opacity: 0;
  transform: translateY(150vh);
}

.review__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  opacity: 0;
  transform: translateY(50vh);
  max-width: 1000px;
}

.review__item {
  margin-right: 40px;
  margin-bottom: 20px;
}

.review__item--more {
  transform: translateY(-200%);
  visibility: hidden;
}

.review__list.open .review__item--more {
  transform: translateY(0);
  visibility: visible;
  transition: 1.5s;
}

.review__button  {
  margin-left: 15%;
  opacity: 0;
  margin-top: 0;
}

.swiper-slide-active .review__box::after {
  transform: translate(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.8s;
}

.swiper-slide-active .review__text {
  opacity: 1;
  transform: translate(0);
  transition: opacity 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.8s ,transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
}

.swiper-slide-active .review__list {
  opacity: 1;
  transform: translate(0);
  transition: opacity 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.8s ,transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
}

.swiper-slide-active .review__button {
  opacity: 1;
  transition: opacity 1s linear 2s;
}

@media (max-width: $mobile-width) {
  .review__title {
    font-size: 60px;
  }

  .review__text {
    font-size: 13px;
    margin-bottom: 30px;
  }

  .review__item {
    margin-right: 0;
    width: 25%;
    padding: 5px;

    & img {
      width: 100%;
    }

    &:nth-child(5) {
      width: 100%;
    }
  }

  .review__button {
    margin-left: 0;
    margin-top: 10px;
  }
}
