$bg: #000;
$glow: #e35d14;

.about-video {
  position: fixed;
  z-index: 5;
  bottom: 5%;
  left: 5%;
  width: 60px;
  color: $prime-color;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 20px;
  text-align: center;
  font-weight: 900;
  -webkit-text-stroke: 1px $prime-color;
  -webkit-text-fill-color: #000;
  -ms-color: #000;
  -ms-text-shadow: -1px -1px 0 $prime-color, 1px -1px 0 $prime-color, -1px 1px 0 $prime-color, 1px 1px 0 $prime-color;
  //animation: neonText 1.5s ease-in-out infinite alternate;

  & img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 2px solid $glow;
    box-shadow: 0 0 0 0 $bg,
                0 0 2px 3px $glow,
                0 0 4px 25px $bg,

                0 0 1px 32px $bg,
                0 0 32px 25px $glow,
                0 0 32px 25px $bg;

  animation: pulse 2s linear infinite;
  transition: all 0.3s ease;
  }

  &.open {
    z-index: 16;
    bottom: auto;
    left: auto;
    top: 5%;
    right: 5%;
    font-size: 0;

    &::after {
      content: 'close';
      color: $prime-color;
      text-decoration: none;
      text-transform: uppercase;
      font-size: 20px;
      font-weight: 900;
      -webkit-text-stroke: 1px $prime-color;
      -webkit-text-fill-color: #000;
      -ms-color: #000;
      -ms-text-shadow: -1px -1px 0 $prime-color, 1px -1px 0 $prime-color, -1px 1px 0 $prime-color, 1px 1px 0 $prime-color;
      transition: all 0.3s ease;
    }

    & img {
      opacity: 0;
      width: 0;
      height: 0;

      &::after {

      }
    }
  }
}

.video-popap {
  display: none;
  position: fixed;
  z-index: 15;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  & video {
    max-width: 100%;
    max-height: 100%;
    width: 1200px;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.video-overlay {
  display: none;
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(black, 0.5);
}


@keyframes pulse {

  0% {
    box-shadow:

      0 0 0 0 $bg,
      0 0 2px 3px $glow,

      0 0 1px 32px $bg,
      0 0 32px 25px $glow;

  }

  49.9% {

    box-shadow:
      0 0 1px 32px $bg,
      0 0 32px 25px $glow,

      0 0 1px 64px $bg,
      0 0 60px 25px $glow
  }

  50% {
    box-shadow:
      0 0 0 0 $bg,
      0 0 2px 3px $glow,


      0 0 1px 32px $bg,
      0 0 32px 25px $glow;
  }

  100% {
    box-shadow:
      0 0 1px 32px $bg,
      0 0 32px 25px $glow,

      0 0 1px 64px $bg,
      0 0 60px 25px $glow;
  }
}


@media (max-width: $mobile-width) {
  .about-video {
    width: 36px;
    top: 150px;
    left: auto;
    right: 30px;
    font-size: 14px;

    & img {
      width: 36px;
      height: 36px;
    }

    &.open {
      top: 150px;
      right: 30px;

      &::after {
        font-size: 14px;
      }
    }
  }

  @keyframes pulse {

    0% {
      box-shadow:

        0 0 0 0 $bg,
        0 0 1px 2px $glow,

        0 0 1px 16px $bg,
        0 0 16px 12px $glow;

    }

    49.9% {

      box-shadow:
        0 0 1px 16px $bg,
        0 0 16px 12px $glow,

        0 0 1px 32px $bg,
        0 0 30px 12px $glow
    }

    50% {
      box-shadow:
        0 0 0 0 $bg,
        0 0 1px 2px $glow,


        0 0 1px 16px $bg,
        0 0 16px 12px $glow;
    }

    100% {
      box-shadow:
        0 0 1px 16px $bg,
        0 0 16px 12px $glow,

        0 0 1px 32px $bg,
        0 0 30px 12px $glow;
    }
  }
}
