.small-page {
  position: relative;
  padding: 150px 0 0;
  color: #fff;
  background: #000;
  min-height: 100vh;

  & a {
    color: #fff;
  }

  & .back {
    top: 0;
  }
}

.small-page--white {
  background: #fff;
  color: #000;

  & a {
    color: #000;
  }
}

.small-page .contacts__box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.small-page .why__box-title {
  margin: 70px 0 5vh;
  text-transform: uppercase;
  font-size: 4vw;
}

.small-page .why-form,
.small-page .contacts__block {
  width: 48%;
  display: block;
}

.small-page .order__title {
  color: $prime-color;
}

.small-page .form {
  width: 100%;
  padding: 20px;
  margin-bottom: 10vh;
}

.small-page .form::after {
  background: none;
  border: 1px solid $prime-color;
  width: 100%;
  transform: skew(-10deg) translateX(-20px);
}

.small-page .form__input {
  border-color: #fff;
}

.small-page .form__button {
  bottom: -43px;
  right: auto;
  left: 0;
  color: #000;

  &::after {
    background: #fff;
  }
}

.small-page .contacts__item a,
.small-page .contacts__item.adres {
  font-size: 2vw;
  margin-bottom: 7vh;
}

.header__contact.active {
  color: $prime-color;
}

.why-form--page {
  position: relative;

  & .back {
    top: -48px;
  }
}

@media (max-width: 1120px) {
  .small-page .contacts__box {
    justify-content: space-evenly;
  }

  .small-page .why-form,
  .small-page .contacts__block {
    width: auto;
  }

  .small-page .contacts__block {
    order: -1;
  }
}

@media (max-width: $mobile-width) {
  .small-page {
    padding-top: 80px;
  }

  .small-page .form {
    background: transparent;

    &::after {
      transform: none;
    }
  }

  .small-page .contacts__item a,
  .small-page .contacts__item.adres {
    font-size: 18px;
    margin-bottom: 20px;
  }

  .small-page .why__box-title {
    margin: 30px 0;
    text-transform: uppercase;
    font-size: 40px;
  }

  .header__contact.active {
    display: none;
  }
}
