.page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #000000;
  color: #fff;
  overflow: hidden;
  touch-action: pan-x;
}

.page__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.full-screen--about {
  background-color: #000;
}

.screen {
  flex: 1 0 100%;
  position: relative;
}

.screen__content {
  padding: 150px 25px 25px;
  height: 100%;
  opacity: 0;
  transition: 1.5s;
}

.swiper-slide-active .screen__content {
  opacity: 1;
  transition: 1.5s;
}

.strelca,
.strelca-top {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  stroke: #707070;
  transition: 0.8s;
  z-index: 4;

  & path {
    fill: #707070;
  }
}

.strelca-top {
  top: 20px;
  bottom: auto;
  transform: rotate(180deg);
  z-index: 11;
}

.strelca:hover,
.top:hover,
.strelca-top:hover {
  stroke: $prime-color;

  & path {
    fill: $prime-color;
  }
}

.strelca.swiper-button-disabled,
.top.swiper-button-disabled {
  transform: translateY(150vh);
}

.strelca-top.swiper-button-disabled {
  transform: translateY(-150vh);
}

.top {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateY(150vh);
  cursor: pointer;
  stroke: #707070;
  transition: 0.8s;

  & path {
    fill: #707070;
  }

  &.active {
    transform: translateX(-50%) rotate(180deg);
  }
}

@media (max-width: $mobile-width) {
  .strelca,
  .top,
  .strelca-top {
    display: none;
  }

  .screen__content {
    padding: 105px 0 0;
  }
}

@media (max-width: 700px), (max-height: 600px) {
  .page {
    overflow-y: visible;
  }

  .page::-webkit-scrollbar {
    width: 0;
  }

  .full-screen {
    height: auto;
    background-color: #000;
  }
}

@media (min-width: 2000px) {
  .full-screen .wrapper {
    max-width: 1600px;
  }
}

@media (max-width: 1480px) {
  .full-screen .wrapper {
    padding: 0 75px;
  }
}

@media (max-width: $mobile-width) {
  .full-screen .wrapper {
    padding: 0 80px 0 20px;
  }

  .full-screen .about-page__wrapper {
    padding: 0 20px 0 60px;
  }
}

@media (max-height: 430px) and (orientation: landscape) {
  .full-screen::after {
    content: "Пожалуйста, переверните устройство для корректной работы сайта";
    font-family: $accent-font;
    font-size: 6vh;
    text-transform: uppercase;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #000;
    color: #fff;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
}
